import React from 'react';
import { Radio, Typography, FormControlLabel, Box } from '@mui/material';


interface CustomRadioLabelProps {
  price: string;
  oldPrice: string;
  discount: string;
  duration: number;
  defaultCurrencySymbol:string
}


const CustomRadioLabel: React.FC<CustomRadioLabelProps> = ({ price, oldPrice, discount, duration,defaultCurrencySymbol }) => {
  return (
    <Box
      sx={{
        borderRadius: '4px',
        padding: '2px',
        display: 'flex',
        flexDirection: 'column', 
        justifyContent: 'space-between',
        width: '100%', 
      }}
    >
     
      <Box 
        sx={{ 
          display: 'flex', 
          justifyContent: 'space-between',
         gap:{sm:"3px"},
          flexWrap: 'wrap', 
        }}
      >
        <Typography 
          variant="h6" 
          sx={{
            fontWeight: 'bold',
            color: '#000',
            fontSize: "16px", 
          }}
        >
          {defaultCurrencySymbol}{price}
        </Typography>
        <Typography 
          variant="body2" 
          sx={{ 
            color: 'gray',
            fontSize: { xs: '12px', sm: '12px', md: '12px' }, 
            fontWeight:"bold"
          }}
        >
          {duration} Months
        </Typography>
      </Box>
      <Box 
        sx={{
          display: 'flex',
          gap: '4px',
          marginTop: '4px',
          flexWrap: 'wrap',
        }}
      >
        <Typography 
          variant="body2" 
          sx={{ 
            textDecoration: 'line-through',
            color: 'red',
            fontSize: { xs: '12px', sm: '14px', md: '14px' },  
            fontWeight: 'bold',
          }}
        >
          {oldPrice}
        </Typography>
        <Typography 
          variant="body2" 
          sx={{ 
            fontWeight:"bold",
            color: 'green', 
            fontSize: { xs: '12px', sm: '14px', md: '14px' },  
          }}
        >
          {discount}% Off
        </Typography>
      </Box>
    </Box>
  );
};

// Define types for the props of the CustomRadioButton component
interface CustomRadioButtonProps {
  price: string;
  oldPrice: string;
  discount: string;
  duration: number;
  value: string;
  selectedValue: string;
  handleChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
  defaultCurrencySymbol:string
}

// Custom radio button with the custom label
const CustomRadioButton: React.FC<CustomRadioButtonProps> = ({
  price,
  oldPrice,
  discount,
  duration,
  value,
  selectedValue,
  handleChange,
  defaultCurrencySymbol
}) => {

  return (
    <FormControlLabel
      value={value}
      control={<Radio checked={selectedValue === value} onChange={handleChange} />}
      label={<CustomRadioLabel price={price} oldPrice={oldPrice} discount={discount} duration={duration} defaultCurrencySymbol={defaultCurrencySymbol}/>}
      sx={{
        backgroundColor: '#f5f5f5', // Background color for the whole row
        borderRadius: '8px',
        padding: '8px', // Adjusted padding
        margin: '4px 0px',
        width: { xs: '100%', sm: '110%', md: '100%' },  // Responsive width
        '& .MuiRadio-root': { marginRight: '8px' }, // Adjust space between radio and label
      }}
    />
  );
};

export default CustomRadioButton;
